<template>
  <div class="main">
    <div class="row">
      <div class="container coursePageAll">
        <div class="coursePageTitle">
          <h3>我的課程</h3>
        </div>
        <Header />
        <p class="notice" v-if="UnfinishClassRoom > 0 && UnfinishHomeWork > 0">
          您還有{{ UnfinishClassRoom }}堂課、{{
            UnfinishHomeWork
          }}份作業未完成唷！
        </p>
        <p
          class="notice"
          v-else-if="UnfinishClassRoom == 0 && UnfinishHomeWork > 0"
        >
          您還有{{ UnfinishHomeWork }}份作業未完成唷！
        </p>
        <p
          class="notice"
          v-else-if="UnfinishClassRoom > 0 && UnfinishHomeWork == 0"
        >
          您還有{{ UnfinishClassRoom }}堂課未完成唷！
        </p>
        <div class="tableStyle1">
          <table>
            <tr>
              <th>開課期間</th>
              <th>教師</th>
              <th width="30%">課程主題</th>
              <th>課程內容</th>
              <th>作業狀態</th>
            </tr>
            <tr>
              <td class="tableNoContent" colspan="5" v-if="ClassRooms.length ==0">目前沒有課程</td>
            </tr>
            <template v-for="c in ClassRooms">
              <tr v-for="(l, i) in c.LessonList" :key="l.LessonID">
                <template v-if="i == 0">
                  <td :rowspan="c.Lessons.length">
                    {{ c.StartTime | dateFormat }}~{{ c.EndTime | dateFormat }}
                  </td>
                  <td :rowspan="c.Lessons.length">
                    <span v-if="c.Teacher">{{ c.Teacher.Name }}</span>
                  </td>
                  <td :rowspan="c.Lessons.length">
                    {{ c.LessonTitle }}<br />({{ c.Code }})
                  </td>
                  <td>
                    <router-link :to="'/lesson/' + l.LessonID">{{
                      l.LessonName
                    }}</router-link>
                  </td>
                  <td v-if="!c.HasHomeWork">-</td>
                  <td v-else>
                    <template v-if="l.HomeWork">
                      <span v-if="l.HomeWork.Answers">
                        {{ l.HomeWork.CorrectCount }}/{{
                          l.HomeWork.TotalCount
                        }}
                      </span>
                      <span v-else-if="c.Expired">未開課</span>
                      <router-link v-else-if="l.Progress == 100" :to="'/homework/' + l.LessonID"
                        >未完成</router-link
                      >
                      <span v-else>未完成</span>
                    </template>
                    <span v-else>-</span>
                  </td>
                </template>
                <template v-else>
                  <td>
                    <router-link :to="'/lesson/' + l.LessonID">{{
                      l.LessonName
                    }}</router-link>
                  </td>
                  <td v-if="!c.HasHomeWork">-</td>
                  <td v-else>
                    <template v-if="l.HomeWork">
                      <span v-if="l.HomeWork.Answers">
                        {{ l.HomeWork.CorrectCount }}/{{
                          l.HomeWork.TotalCount
                        }}
                      </span>
                      <span v-else-if="c.Expired">未開課</span>
                      <router-link v-else-if="l.Progress == 100" :to="'/homework/' + l.LessonID"
                        >未完成</router-link
                      >
                      <span v-else>未完成</span>
                    </template>
                    <span v-else>-</span>
                  </td>
                </template>
              </tr>
            </template>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Header from "@/components/Student/Header.vue";
import moment from "moment";

export default {
  components: {
    Header,
  },
  data() {
    return {
      ClassRooms: [],
      UnfinishClassRoom: 0,
      UnfinishHomeWork: 0,
    };
  },
  mounted() {
    this.setBase("sSpace page");
    this.load();
  },
  methods: {
    async load() {
      var response = await this.$api.getMyClassRoom();
      var map = {},
        memberMap = {},
        homeworkMap = {},
        mcMap = {},
        mlMap = {};
      response.HomeWorks.forEach((h) => (homeworkMap[h.LessonID] = h));
      response.MemberLesson.forEach(l => mlMap[`${l.ClassRoomID}-${l.LessonID}`] = l);
      response.Lessons.forEach((l) => {
        map[l.LessonID] = l;
        l.HomeWork = homeworkMap[l.LessonID];
        if (l.HomeWork && !l.HomeWork.Answers) {
          this.UnfinishHomeWork++;
        }
      });
      response.Members.forEach((m) => (memberMap[m.MemberID] = m));
      response.MyClassRooms.forEach((m) => (mcMap[m.ClassRoomID] = m));
      response.ClassRooms.forEach((c) => {
        var list = [];
        c.Lessons.forEach((l) => list.push(map[l]));
        list.forEach(l=>{
          l.Progress = mlMap[`${c.ClassRoomID}-${l.LessonID}`].Progress || 0;
        });
        c.LessonList = list;
        c.Teacher = memberMap[c.TeacherMemberID];
        c.Expired = moment().isAfter(c.EndTime);
        c.Progress = 0;
        c.FinishTime = null;
        if (mcMap[c.ClassRoomID]) {
          c.Progress = mcMap[c.ClassRoomID].Progress;
          c.FinishTime = mcMap[c.ClassRoomID].FinishTime;
        }

        if (!c.FinishTime) {
          this.UnfinishClassRoom++;
        }
      });
      this.ClassRooms = response.ClassRooms;
    },
    copy(c) {
      window.utils.copy(c.Code);
      setTimeout(() => {
       alert("課程代碼已複製"); 
      });
    },
    async remove(c) {
      await this.$api.delClassRoom(c.ClassRoomID);
      this.load();
    },
  },
};
</script>